.intro{
    background-color: #103a70;
    padding: 20px;
    color: white;
    height: 1000px;
    width: 100%;
    overflow: hidden;
}

.intro-img{
    height: 500px;
    width: 500px;
}

.into-container{
    margin-top: 200px;
}

.into-text{
    margin-top: 16vh;
    margin-left: 100px;
}

@media only screen and (max-width: 900px){
    .into-container{
        margin-top: 1px;
    }
    .intro-img{
        margin-top: 100px;
        height: 300px;
        width: 300px;
    }
    .into-text{
        margin-top: 80px;
        margin-left: 0px;
    }
}