.navigation{
    background-color: #0a2b57;
    width: 100%;
    top: 0px;
    overflow: hidden;
}
.logo{
    color:#f2fcfc;
    font-size: 24px;
    padding: 3px;
    font-weight: bold;
}

.desktop{
    list-style-type: none;
    float: right;
    color: white;
    margin-top: 10px;
}

.menu-items{
    font-size: 18px;
    display: inline;
    padding: 14px 16px;
    cursor: pointer;
}

.menu-items:hover{
    cursor: pointer;
    color: #33b0a8;
    transition: 1s;
}

.mobile{
    display: none;
}

.mobile-icon{
    display: none;
}
.mobile-icon-button{
    display: none;
}

@media only screen and (max-width: 700px){
    .desktop{
        margin-top: 10px;
    }
    .menu-items{
        display: block;
        float: left;
    }
    .mobile-icon-button{
        display: block;
        border: none;
        background-color: #0a2b57;
        cursor: pointer;
        float: right;
        outline: none;
    }
    .mobile-icon-button:hover{
        border: none;
        outline: none;
    }
    .mobile-icon-button:focus{
        border: none;
        outline: none;
    }
    .mobile-icon{
        display: inline-block;
        padding: 5px;
        color: white;
        font-size: 25px;
    }
    .mobile{
        display: block;
    }
    .desktop{
        display: none;
    }
    .active{
        display: block;
    }
}