.skills{
    background-color: #f2fcfc;
    color: black;
    width: 100%;
    height: 600px;
}

.skills-image{
    margin-top: 150px;
}

.skills-img{
    height: 350px;
    width: 350px;
}

.skills-text{
    margin-top: 100px;
}

.skills h2{
    color: #103a70;
    font-size: 30px;
    font-weight: bold;
}

.skills p{
    font-size: 20px;
}

@media only screen and (max-width: 899px){
    .skills{
        height: 1000px;
    }
    .skills-image{
        margin-top: 10px;
    }
    .skills p{
        padding: 9px;
    }
}